.LoadingBar {
  height: 15px;
  width: 100px;
  border-radius: 4px;
  background: linear-gradient(324deg, #cccccc, #444444);
  background-size: 600% 600%;

  -webkit-animation: loadingbarAnimation 2s ease infinite;
  -moz-animation: loadingbarAnimation 2s ease infinite;
  -o-animation: loadingbarAnimation 2s ease infinite;
  animation: loadingbarAnimation 2s ease infinite;
}

@-webkit-keyframes loadingbarAnimation {
  0% {
    background-position: 18% 0%;
  }
  50% {
    background-position: 83% 100%;
  }
  100% {
    background-position: 18% 0%;
  }
}
@-moz-keyframes loadingbarAnimation {
  0% {
    background-position: 18% 0%;
  }
  50% {
    background-position: 83% 100%;
  }
  100% {
    background-position: 18% 0%;
  }
}
@-o-keyframes loadingbarAnimation {
  0% {
    background-position: 18% 0%;
  }
  50% {
    background-position: 83% 100%;
  }
  100% {
    background-position: 18% 0%;
  }
}
@keyframes loadingbarAnimation {
  0% {
    background-position: 18% 0%;
  }
  50% {
    background-position: 83% 100%;
  }
  100% {
    background-position: 18% 0%;
  }
}
