.CreatePost {
  width: 80%;
  height: 400px;
  margin: auto;
  box-shadow: 4px 4px 5px 1px #444;
  padding: 15px;
}

.Header {
  display: flex;
}
.Header span {
  margin: auto 15px;
  font-size: 1.6rem;
}
.Content {
  display: flex;
  flex-flow: column;
  margin: 10px;
  box-sizing: border-box;
  height: 250px;
}

.Content input {
  border-radius: 5px;
  border: 1px solid #ccc;
  margin: 3px 0px;
  height: 2rem;
  font-size: 1.7rem;
  padding: 2px 10px;
  font-family: inherit;
}

.Content textarea {
  border-radius: 5px;
  border: 1px solid #ccc;
  margin: 3px 0px;
  height: calc(250px - 2rem);
  font-size: 1.7rem;
  padding: 2px 10px;
  font-family: inherit;
}

.Footer {
  display: flex;
  margin: 10px;
  box-sizing: border-box;
  flex-flow: row-reverse;
}

@media (max-width: 499px) {
  .CreatePost {
    width: 95%;
    box-shadow: 1px 1px 2px 1px #444;
    margin: 0px;
    box-sizing: border-box;
    height: 420px;
  }
}
