.NavigationItem {
  margin: 10px 0;
  box-sizing: border-box;
  display: block;
  width: 100%;
}

.NavigationItem a {
  color: #255f96;
  text-decoration: none;
  width: 100%;
  box-sizing: border-box;
  display: block;
}
.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
  color: #40a4c8;
}

@media (min-width: 500px) {
  .NavigationItem {
    margin: 0;
    width: auto;
    display: flex;
    height: 100%;
    align-items: center;
  }

  .NavigationItem a {
    color: white;
    width: auto;
    height: 100%;
    padding: 16px 10px;
    border-bottom: 4px solid transparent;
  }
  .NavigationItem a:hover,
  .NavigationItem a:active,
  .NavigationItem a.active {
    border-bottom-color: #40a4c8;
    background-color: #255f96;
    color: white;
  }
}
