.Button {
  background-color: transparent;
  border: none;
  color: white;
  outline: none;
  cursor: pointer;
  font: inherit;
  padding: 10px;
  margin: 10px;
  font-weight: bold;
}

.Success {
  background-color: #105392;
  box-shadow: 1px 1px 2px #105392;
}

.Danger {
  background-color: #944317;
  box-shadow: 1px 1px 2px #944317;
}
.Button.Success:hover {
  background-color: white;
  color: #105392;
  border: 1px solid #105392;
  box-shadow: inset 1px 1px 2px #105392;
}

.Button.Danger:hover {
  background-color: white;
  color: #944317;
  border: 1px solid #944317;
  box-shadow: inset 1px 1px 2px #944317;
}
