.Post {
  width: 80%;
  height: 400px;
  margin: 20px auto;
  box-shadow: 4px 4px 5px 1px #444;
  padding: 15px;
}

.Header {
  display: flex;
}
.Header span {
  margin: auto 15px;
  font-size: 1.6rem;
}
.Content {
  display: flex;
  flex-flow: column;
  margin: 10px;
  box-sizing: border-box;
  height: 250px;
}

.Content .PostTitle {
  margin: 3px 0px;
  height: 4.4rem;
  padding: 2px 10px;
  box-sizing: border-box;
  font-weight: bold;
  font-size: 1.7rem;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
}
.Content .PostContent {
  margin: 3px 0px;
  height: calc(250px - 4.4rem);

  border: 1px solid #ccc;
  box-shadow: 0px 0px 2px #ccc;
  padding: 2px 10px;
  overflow-x: scroll;
}

@media (max-width: 499px) {
  .Post {
    width: 95%;
    box-shadow: 1px 1px 2px 1px #444;
    margin: 20px 0px;
    box-sizing: border-box;
    height: 420px;
  }
}
