.LetterIcon {
  border-radius: 32px;
  background-color: red;
  width: 64px;
  height: 64px;
  font-size: 1.6rem;
  font-weight: bold;
  color: white;
  display: flex;
  align-items: center;
  box-shadow: 1px 1px 3px black;
}
.LetterIcon span {
  width: 100%;
  text-align: center;
  margin: auto;
}
