.LoadingPost {
  width: 80%;
  height: 400px;
  margin: 20px auto;
  box-shadow: 4px 4px 5px 1px #444;
  padding: 15px;
}

.Header {
  display: flex;
}
.Header span {
  margin: auto 15px;
  font-size: 1.6rem;
}
.Content {
  display: flex;
  flex-flow: column;
  margin: 10px;
  box-sizing: border-box;
  height: 250px;
}

.Content .PostTitle {
  margin: 3px 0px;
  height: 2rem;
  padding: 2px 10px;
}

.Content .PostContent {
  margin: 3px 0px;
  height: calc(250px - 2rem);

  padding: 2px 10px;
}

@media (max-width: 499px) {
  .LoadingPost {
    width: 95%;
    box-shadow: 1px 1px 2px 1px #444;
    margin: 20px 0px;
    box-sizing: border-box;
    height: 420px;
  }
}
